import React from 'react';
import Header from './components/header';
import {Route} from 'react-router-dom';
import RecipePage from './containers/recipePage';
import Recipes from './containers/recipes';
import './App.css';

function App() {

  return (
    <div className="App">
      <Header/>
      <Route exact path="/recipe-project" component={Recipes}/>
      <Route exact path="/recipe-project/:id" component={RecipePage}/>
    </div>
  );
}

export default App;
